@import url('https://fonts.googleapis.com/css2?family=Monoton&display=swap');

body {
  width: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

header {
  padding: 2.4rem;
  background: linear-gradient(90deg, #00CED1, #4682B4 55%);
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
}

h1 {
  margin: 0 2.0rem;
  color: #333;
  font-family: 'Monoton', cursive;
  font-weight: bold;
  font-size: 3.0rem;
  line-height: 1;
}

.search-box input {
  margin-top: 6px;
  margin-right: 2.0rem;
  padding: 1.2rem 1.2rem 1.2rem 0.6rem;
  width: 14rem;
  height: 1.2rem;
  border: none;
  border-bottom: 1px solid #222;
  outline: none;
  color: #000;
  background: transparent;
}

main { width: 100% }

.card-list {
  margin: 0 auto;
  margin-top: 24px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.card {
  width: 230px;
  margin: 0.6rem;
  padding: 1.5rem;
  border-radius: 1.0rem;
  background: #FFF8DC;
  text-align: center;
  display: inline-block;
}

h2 {
  width: 100%;
  margin: 0 auto;
  font-size: 1.2rem;
}

p { margin: 0 }

.card img {
  width: 200px;
  height: 200px;
  margin-bottom: 24px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
